import {
	hostUrl
} from '@/http/request.js';
import {mapActions} from 'vuex';
export default {
	props:{
		shopList:{
			type:Array,
			default:() => {
				return []
			}
		}
	},
	data(){
		return {
			shopCartImg:'/static/images/goodListImg/shopcart.png',
			hostUrl:hostUrl
		}
	},
		
	methods:{
			...mapActions(['getIntegralAddCart']),
		addCart(id){
			// 验证登陆信息
			let userId = uni.getStorageSync('userId');
			if(!userId){
				uni.showToast({
					title:'请先登陆',
					icon:'none'
				})
				return 
			}
		
		
			let {shopList} = this;
				
			let info = shopList.find(item =>{
				return item.id === id;
			})
		
			
			let seleteNumber = 1;
			if(seleteNumber > info.stock){
				uni.showToast({
					title:'库存不足',
					icon:'none'
				})
				return
			}
			// 加入购物车
			let option = {
				id:info.id,
				userId:userId,
				quantity:seleteNumber,
				isChecked:1,
				unit:'份'
			}
			this.getIntegralAddCart(option);
		}
	},
	
	
}
